import React from 'react'
import Carousel from 'react-bootstrap/Carousel'

const sliderGenerator = (img,title,text,link='',atagBoolean=false)=>{
    return(
        <Carousel.Item interval={3000}>
        <img
        className="d-block w-100"
        src={img}
        alt="slide"
        />
        <Carousel.Caption>
        <h3>{title}</h3>
        <p>{text}</p>
        { atagBoolean  && <a href={link} style={{color:"white"}}>VISIT</a>}
        </Carousel.Caption>
    </Carousel.Item>
    )
}

